import React from "react"
import styled from "styled-components"
import i18next from "i18next"
const WrapFilter = styled.div`
  position: relative;
  & > .actions {
    // position: absolute;
    // z-index: 1;
    // top: -40px;
    // left: 80px;
    // height: 34px;
    overflow: hidden;
    // padding: 0px 1.25rem;
    width: 100%;
    display: flex;
    flex: row;
    align-items: center;
    margin-bottom: 10px;
    flex-wrap: wrap;
    & > button {
      height: 34px;
      width: 34px;
      border-radius: 17px;
      padding: 2px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 0.5rem;
    }
    & > button.inactive {
      background: white;
      border: white;
      color: #37a8e8;
    }
    & .a-item {
      margin-bottom: 5px;
      background: white;
      padding: 5px 10px;
      border-radius: 20px;
      height: 30px;
      display: flex;
      align-items: center;
      margin-right: 0.5rem;
      padding-right: 5px;
      box-shadow: rgb(27 31 35 / 10%) 2px 2px 2px 0px;
      & span {
        width: 23px;
        height: 30px;
        margin-left: 5px;
        border-left: 1px solid #f1f4f6;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        & :hover {
          color: red;
        }
      }
      & span.a-title {
        width: auto;
        background: #6088b3;
        color: white;
        margin-left: -10px;
        padding-left: 10px;
        border-top-left-radius: 30px;
        border-bottom-left-radius: 30px;
        overflow: hidden;
        padding-right: 5px;
        margin-right: 5px;
        padding-top: 5px;
        font-size: 13px;
        max-width: 100px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        display: inline-block;
      }
    }
  }
`
export default function MUIATFilter(props) {
  const { cfp } = props
  // const [showFilter, setShowFilter] = React.useState(false)
  return (
    <WrapFilter className="mpat-filter">
      {cfp.onePage &&
        cfp.onePage._manualFilterOuts &&
        cfp.onePage._manualFilterOuts.length > 0 && (
          <div className="actions">
            {cfp.onePage &&
              cfp.onePage._manualFilterOuts &&
              cfp.onePage._manualFilterOuts.map((v, i) => {
                // console.warn("v:", v)
                let _styleTitleOut = {}
                let _showTitle = true
                if (v.more) {
                  if (v.more.showTitle != null) {
                    _showTitle = v.more.showTitle
                  }
                  if (v.more.styleTitleOut) {
                    _styleTitleOut = v.more.styleTitleOut
                  }
                }
                return (
                  <div key={i} className="a-item">
                    {_showTitle && (
                      <span
                        className="a-title"
                        title={v.title}
                        style={_styleTitleOut}
                      >
                        {v.title}
                      </span>
                    )}
                    {v.valueDisplay}
                    <span
                      onClick={() => {
                        console.warn("clear", v)
                        cfp.handleClearFilter(v)
                      }}
                    >
                      <i className="fa fa-times" />
                    </span>
                  </div>
                )
              })}
            {cfp.onePage &&
              cfp.onePage._manualFilterOuts &&
              cfp.onePage._manualFilterOuts.length > 0 && (
                <div
                  key={"clear"}
                  className="a-item"
                  style={{ color: "#dc3545", cursor: "pointer" }}
                  onClick={() => {
                    cfp.handleClearFilter()
                  }}
                >
                  {i18next.t("Clear Filter")}
                  <span>
                    <i className="fa fa-times" />
                  </span>
                </div>
              )}
          </div>
        )}

      {/* {showFilter === true && (
        <div className="card" style={{ marginBottom: "1rem" }}>
          <div className="card-body">Filter</div>
        </div>
      )} */}
    </WrapFilter>
  )
}
